@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Turret+Road&display=swap);
/*!Reset default styles!*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  border-bottom-color: #fff;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/*!--------------------!*/

body {
  position: relative;
  font-family: 'Roboto', sans-serif;
  background-color: rgba(181, 181, 189, 0.541);
  color: rgb(105, 104, 104);
  padding: 1rem 0px 0px 0px;
}

