:root {
  --rad: 0.7rem;
  --dur: 0.3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #d0d0de;
  --height: 4rem;
  --btn-width: 9.5rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

// Main styles
.form_search {
  position: relative;
  width: 18rem;
  border-radius: var(--rad);
  transform: translate(0%, 20%);
}
.input_search,
.button_search {
  height: var(--height);
  border: 0;
  color: var(--color-dark);
  font-size: 1.3rem;
}
.input_search[type='search'] {
  outline: 0;
  width: 100%;
  background: var(--color-light);
  padding: 0 1.6rem;
  border-radius: var(--rad);
  appearance: none;
  transition: all var(--dur) var(--bez);
  transition-property: width, border-radius;
  z-index: 1;
  position: relative;
}
.button_search {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: var(--btn-width);
  background: var(--color-brand);
  border-radius: 0 var(--rad) var(--rad) 0;
}
.input_search:not(:placeholder-shown) {
  border-radius: var(--rad) 0 0 var(--rad);
  width: calc(115% - var(--btn-width));
  + .button_search {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 1.1rem;
  }
}
